'use client';
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';

const fetchTimeslots = async () => {
  try {
    const response = await axios.get("http://localhost:9921/timeslots");
    console.log("Fetched timeslots:", response.data);
    return response.data;
  } catch (error) {
    console.error("Error fetching timeslots:", error);
    return [];
  }
};

const TimeslotsPage = () => {
  const [timeslots, setTimeslots] = useState([]);
  const [filteredTimeslots, setFilteredTimeslots] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeCategory, setActiveCategory] = useState("All Timeslots");
  const router = useRouter();

  useEffect(() => {
    const getTimeslots = async () => {
      const data = await fetchTimeslots();
      setTimeslots(data);
      setFilteredTimeslots(data);
      setLoading(false);
    };
    getTimeslots();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (category === "All Timeslots") {
      setFilteredTimeslots(timeslots);
    } else {
      const filtered = timeslots.filter((timeslot) => timeslot.timeslotType === category);
      setFilteredTimeslots(filtered);
    }
  };

  const handleDeleteTimeslot = async (timeslotId) => {
    try {
      await axios.delete(`http://localhost:9921/timeslots/${timeslotId}`);
      const updatedTimeslots = timeslots.filter((timeslot) => timeslot.timeslotId !== timeslotId);
      setTimeslots(updatedTimeslots);
      setFilteredTimeslots(updatedTimeslots);
    } catch (error) {
      console.error("Error deleting timeslot:", error);
    }
  };

  const handleEditTimeslot = (timeslotId) => {
    router.push(`/timeslots/edit/${timeslotId}`);
  };

  return (
    <div className="p-4 sm:p-6">
      {/* Header */}
      <div className="mb-6 flex flex-col sm:flex-row sm:justify-between sm:items-center">
        <div>
          <h3 className="text-lg text-green-900 font-semibold">Timeslots Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available timeslots</p>
        </div>
        <button
          onClick={() => router.push('/timeslots/add')}
          className="mt-4 sm:mt-0 px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700">
          Add New Timeslot
        </button>
      </div>

      {/* Category Filters */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="p-4 border-b flex flex-wrap gap-2 sm:gap-4">
          {["All Timeslots"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 rounded-md text-sm font-medium transition ${
                activeCategory === category
                  ? "bg-indigo-100 text-indigo-600"
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
              }`}
              onClick={() => handleCategoryClick(category)}
            >
              {category}
            </button>
          ))}
        </div>

        {/* Timeslot Table */}
        {loading ? (
          <div className="p-4 text-center text-gray-500">Loading timeslots...</div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full min-w-[600px] sm:min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Timeslot ID
                  </th>
                  <th className="px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Star Time
                  </th>
                  <th className="px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    End Time
                  </th>
                  <th className="px-4 sm:px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredTimeslots.length > 0 ? (
                  filteredTimeslots.map((timeslot) => (
                    <tr key={timeslot.timeslotId} className="hover:bg-gray-100">
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {timeslot.timeslotId}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {timeslot.startTime}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {timeslot.endTime}
                      </td>
                      <td className="px-4 sm:px-6 py-4 whitespace-nowrap text-sm">
                        <div className="flex gap-2">
                          <button
                            className="text-indigo-600 hover:text-indigo-900"
                            onClick={() => handleEditTimeslot(timeslot.timeslotId)}
                          >
                            Edit
                          </button>
                          <button
                            className="text-red-600 hover:text-red-900"
                            onClick={() => handleDeleteTimeslot(timeslot.timeslotId)}
                          >
                            Delete
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="5" className="px-4 sm:px-6 py-4 text-center text-gray-500">
                      No timeslots found.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default TimeslotsPage;
